@import url(https://fonts.googleapis.com/css2?family=Lora&family=Rubik:wght@300;400&display=swap);
#quote-box,#text-container{display:flex;flex-direction:column}#text-container{justify-content:flex-start}#buttons-container{justify-content:space-between}#quote-box{width:50%;min-width:200px;max-width:500px;padding:2rem 3.5rem;background:#fff;border-radius:5px}#text{font-size:24px;margin-bottom:1rem}#author{text-align:right;margin-bottom:1.5rem}#new-quote,#tweet-icon{cursor:pointer;transition:opacity 250ms}#new-quote:hover,#new-quote:focus,#tweet-icon:hover,#tweet-icon:focus{opacity:0.7}#new-quote{color:#fff;padding:0.75rem 1.25rem;border-radius:5px;font-family:"Rubik",sans-serif,Arial;outline:none;border:none}@media (max-width: 750px){#text{font-size:20px}#quote-box{padding:1rem 2rem}}*{margin:0}body{font-family:"Lora",sans-serif,Arial;font-size:16px}#main-container,#buttons-container{display:flex;align-items:center}#main-container{height:100vh;justify-content:center}

